import { useDispatch } from 'react-redux';
import { addNewUsers } from '../action';

const AddUser = () => {
  const dispatch = useDispatch();

  const formHandler = (event) => {
    event.preventDefault();
    let data = event.target.elements;
    dispatch(addNewUsers(data.passport.value, data.name.value, data.age.value));
  }

  return (
    <div>
      <form onSubmit={formHandler}>
        <div>
          <input type="text" name="passport" placeholder="Серия и номер паспорта" />
        </div>
        <div>
          <input type="text" name="name" placeholder="Ваше имя" />
        </div>
        <div>
          <input type="text" name="age" placeholder="Ваш возраст" />
        </div>
        <div><button type="submit">Добавить нового пользователя</button></div>
      </form>
    </div>
  );
}

export default AddUser;